<template>
  <div class="admin-addresses-list">
    <div class="admin-addresses-list__filters">
      <Input
        hide-shadow
        v-model="filters.search"
        :placeholder="t('common.search')"
        class="admin-addresses-list__filters__search"
      >
        <template #prepend>
          <Icon name="search" />
        </template>
      </Input>
      <RadioButtons v-model="filters.status" :data="addressesBlacklistRadioFilter">
        <template #default="{ item }">
          {{ t(item.title) }}
        </template>
      </RadioButtons>
      <SelectInput
        muted
        :placeholder="t('blacklists.addressesTable.filter.sortByCrypto')"
        icon="chevron-down"
        class="admin-addresses-list__filters__sort"
      />
      <Button
        type="primary"
        font-size="14px"
        class="admin-addresses-list__filters__add"
        @click="addExpand"
      >
        {{ t('blacklists.addressesTable.add') }}
      </Button>
    </div>
    <Table :columns="columns" :data="data" class="admin-addresses-list__table">
      <template #add>
        <AddAddressItem @add="addAddressItem" />
      </template>
      <template #column="{ column }">
        {{ t(column.title) }}
      </template>
      <template #cell="{ item, column, row }">
        <Badge
          v-if="column.name === 'status'"
          cursor-pointer
          :type="statuses[item].color"
          @click="changeStatus(row)"
        >
          {{ t(statuses[item].text) }}
        </Badge>
        <div v-else :class="{
          'admin-addresses-list__table__muted-row': row.status === 'disabled'
        }">
          <Date
            v-if="column.name === 'date'"
            :date="item"
            format="DD MMM YYYY HH:mm"
          />
          <span v-else>{{ item }}</span>
        </div>
      </template>
    </Table>
  </div>
</template>

<script>
import { reactive } from 'vue'
import { useI18n } from 'vue-i18n'
import { useActions, useGetters } from '@/plugins/vuex'
import { useEvent } from '@/plugins/event/useEvent'
import { addressesBlacklistRadioFilter } from '@/store/mock/blacklistsMock'
import { MODULES_NAMES } from '@/common/modules'
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'
import RadioButtons from '@/components/UIKit/RadioButtons'
import SelectInput from '@/components/UIKit/Select/SelectInput'
import Button from '@/components/UIKit/Button'
import Table from '@/components/Table/Table'
import events from '@/common/events'
import AddAddressItem from '@/views-admin/settings/Blacklists/AddAddressItem'
import Badge from '@/components/UIKit/Badge'
import Date from '@/components/Text/Date'

export default {
  name: 'AddressesList',
  components: { Date, AddAddressItem, Button, SelectInput, Input, Icon, RadioButtons, Table, Badge },
  setup () {
    const { t } = useI18n()
    const {
      getAddressesBlacklistData: data,
      getVisibleAddressesBlacklistColumns: columns
    } = useGetters(MODULES_NAMES.BLACKLISTS)
    const {
      setMockAddressesBlacklistData,
      addAddressItem
    } = useActions(MODULES_NAMES.BLACKLISTS)

    const { emit: addExpand } = useEvent(events.TABLE_ADD)

    const filters = reactive({
      search: '',
      status: null
    })
    const statuses = {
      active: {
        text: 'blacklists.addressesTable.status.active',
        color: 'success'
      },
      disabled: {
        text: 'blacklists.addressesTable.status.disabled',
        color: 'default'
      }
    }

    const changeStatus = (row) => {
      row.status = row.status === 'active' ? 'disabled' : 'active'
    }

    setMockAddressesBlacklistData()

    return {
      t,
      filters,
      statuses,
      data,
      columns,
      addressesBlacklistRadioFilter,
      addExpand,
      addAddressItem,
      changeStatus
    }
  }
}
</script>

<style lang="scss">
.admin-addresses-list {
  margin-top: 30px;

  &__filters {
    display: flex;

    &__search {
      flex-grow: 1;
      min-width: 450px;
      margin-right: 20px;
    }
    &__sort {
      margin-left: 20px;

      .select-input__body__append {
        margin-left: 3px;
      }
    }
    &__add {
      margin-left: 20px;
      padding: 10px 20px;
    }
  }
  &__table {
    margin-top: 18px;

    &__muted-row {
      opacity: 0.4;
    }
  }
}
</style>
