<template>
  <div class="add-address-item">
    <Input
      v-model="form.address.value"
      hide-shadow
      :error="form.address.error"
      :error-text="form.address.errorText"
      :placeholder="t('blacklists.addressesTable.columns.address')"
      class="add-address-item__input add-address-item__input-address"
      @blur="form.address.validate"
    >
    </Input>
    <Input
      hide-shadow
      v-model="form.crypto.value"
      :placeholder="t('blacklists.addressesTable.columns.crypto')"
      class="add-address-item__input add-address-item__input-crypto"
    >
    </Input>
    <Input
      hide-shadow
      v-model="form.reason.value"
      :placeholder="t('blacklists.addressesTable.columns.reason')"
      class="add-address-item__input add-address-item__input-reason"
    >
    </Input>
    <Button
      type="primary"
      class="add-address-item__action"
      @click="add"
    >{{ t('common.add') }}</Button>
  </div>
</template>

<script>
import { reactive } from 'vue'
import { useI18n } from 'vue-i18n'
import Input from '@/components/UIKit/Input'
import Button from '@/components/UIKit/Button'
import { useGetters } from '@/plugins/vuex'
import { MODULES_NAMES } from '@/common/modules'

export default {
  name: 'AddAddressItem',
  components: { Button, Input },
  emits: ['add'],
  setup (_, { emit }) {
    const { t } = useI18n()
    const {
      getAddressesBlacklistData: data
    } = useGetters(MODULES_NAMES.BLACKLISTS)
    const form = reactive({
      address: {
        value: 'c15pJxcAIzaSyB9puDplMxJ62vYlFruxmmIAez3',
        error: false,
        errorText: t('blacklists.addressesTable.error.address'),
        validate () {
          const value = form.address.value
          if (data.value.filter(el => el.address === value).length) {
            form.address.error = true
            return
          }
          form.address.error = false
        }
      },
      crypto: {
        value: ''
      },
      reason: {
        value: ''
      }
    })

    const validate = () => {
      Object
        .keys(form)
        .forEach(item => form[item].validate && form[item].validate())
      return Object
        .keys(form)
        .filter(item => form[item].error)
        .length
    }

    const clearForm = () => {
      Object.keys(form).forEach(item => {
        form[item].value = ''
      })
    }

    const add = () => {
      if (validate()) {
        return
      }
      const row = Object.keys(form).reduce((acc, item) => ({ ...acc, [item]: form[item].value }), {})
      row.id = new Date()
      row.date = new Date()
      row.status = 'active'
      emit('add', row)
      clearForm()
    }

    return {
      t,
      form,
      add
    }
  }
}
</script>

