.add-address-item {
  display: flex;
  padding: 21px 20px 31px 20px;

  &__input {

    &-address {
      min-width: 380px;
    }
    &-crypto {
      margin-left: 20px;
      margin-right: 10px;
    }
    &-reason {
      min-width: 425px;
      margin-right: 20px;
    }
  }
  &__action {
    padding: 10px 20px;
  }
}
</style>
